___CSS_LOADER_EXPORT___.push([module.id, `.kalkulator-headline {
    text-align: center;
    padding: 0 50px;
    max-width: 700px;
    margin: 0 auto;
}

.green-text {
    color: #2C9D57;
}

.kalkulator-headline p {
    margin-top: 25px;
    font-size: 18px;
}

.kalkulator-header-img {
    display: flex;
    justify-content: space-between;
}

.kalkulator-header-img img {
    width: 180px;
}

.green-line {
    border: none;
    border-top: 3px solid #2C9D57;
}

.calculator__container{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 0;
    margin-top: 50px;
}

.calculator{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.calculator__input{
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.label-lampu, .label-ac, .label-kulkas, .label-kulkas, .label-kendaraan, .label-makanan {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input__lampu, .input__ac, .input__kulkas, .input__kulkas, .input__kendaraan, .input__makanan {
  width: 400px;
  padding: 15px;
  border: none;
  border-radius: 4px;
  box-shadow: 2px 2px 7px 0 rgb(0, 0, 0, 0.2);
  outline: none;
  color: dimgray;
  font-size: 16px;
  margin-bottom: 20px;
}

.hitung {
    margin-top: 20px;
    padding: 14px 40px;
    background-color: #2C9D57;
    border: none;
    border-radius: 50px;
    color: white;
    cursor: pointer;
    width: 100%;
    font-size: 16px;
}

.calculator-output {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.5em;
    margin: 75px;
}

.calculator-output p {
    text-align: center;
    margin-bottom: 20px;
}

#total-emisi {
    font-weight: 600;
    font-size: 1.5em;
    font-family: 'Poppins', sans-serif;
    display: inline;
}

.satuan-total-emisi {
    font-weight: normal;
    font-size: 0.7em;
    font-family: 'Inter';
}


/* responsive rules */
@media only screen and (max-width: 640px) {
    .kalkulator-headline {
        padding: 0 30px;
    }

    .input__lampu, .input__ac, .input__kulkas, .input__kendaraan, .input__makanan {
        max-width: 250px;
    }

    #total-emisi{
        display: block;
    }

}`, "",{"version":3,"sources":["webpack://./src/styles/kalkulator.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,YAAY;EACZ,kBAAkB;EAClB,2CAA2C;EAC3C,aAAa;EACb,cAAc;EACd,eAAe;EACf,mBAAmB;AACrB;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,yBAAyB;IACzB,YAAY;IACZ,mBAAmB;IACnB,YAAY;IACZ,eAAe;IACf,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,kCAAkC;IAClC,eAAe;AACnB;;AAEA;IACI,mBAAmB;IACnB,gBAAgB;IAChB,oBAAoB;AACxB;;;AAGA,qBAAqB;AACrB;IACI;QACI,eAAe;IACnB;;IAEA;QACI,gBAAgB;IACpB;;IAEA;QACI,cAAc;IAClB;;AAEJ","sourcesContent":[".kalkulator-headline {\n    text-align: center;\n    padding: 0 50px;\n    max-width: 700px;\n    margin: 0 auto;\n}\n\n.green-text {\n    color: #2C9D57;\n}\n\n.kalkulator-headline p {\n    margin-top: 25px;\n    font-size: 18px;\n}\n\n.kalkulator-header-img {\n    display: flex;\n    justify-content: space-between;\n}\n\n.kalkulator-header-img img {\n    width: 180px;\n}\n\n.green-line {\n    border: none;\n    border-top: 3px solid #2C9D57;\n}\n\n.calculator__container{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 50px 0;\n    margin-top: 50px;\n}\n\n.calculator{\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\n.calculator__input{\n    margin-bottom: 50px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-directio