    text-align: center;
    padding: 0 50px;
    max-width: 700px;
    margin: 0 auto;
}

.green-text {
    color: #2C9D57;
}

.header-headline p {
    margin-top: 25px;
    font-size: 18px;
}

.header-img {
    display: flex;
    justify-content: space-between;
}

.header-img img {
    width: 180px;
}

.green-line {
    border: none;
    border-top: 3px solid #2C9D57;
}

.profile-desc {
    padding: 50px;
    max-width: 700px;
    margin: 0 auto;
}

.profile-img {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    padding: 0 20px 20px;
}

.member {
    text-align: center;
}

.member img {
    width: 350px;
}

.member h3 {
    margin: 10px 0 5px 0;
}

.member p {
    margin: 0;
    color: gray;
}`, "",{"version":3,"sources":["webpack://./src/styles/tentang-kami.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,oBAAoB;AACxB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,SAAS;IACT,WAAW;AACf","sourcesContent":[".header-headline {\n    text-align: center;\n    padding: 0 50px;\n    max-width: 700px;\n    margin: 0 auto;\n}\n\n.green-text {\n    color: #2C9D57;\n}\n\n.header-headline p {\n    margin-top: 25px;\n    font-size: 18px;\n}\n\n.header-img {\n    display: flex;\n    justify-content: space-between;\n}\n\n.header-img img {\n    width: 180px;\n}\n\n.green-line {\n    border: none;\n    border-top: 3px solid #2C9D57;\n}\n\n.profile-desc {\n    padding: 50px;\n    max-width: 700px;\n    margin: 0 auto;\n}\n\n.profile-img {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n    padding: 0 20px 20px;\n}\n\n.member {\n    text-align: center;\n}\n\n.member img {\n    width: 350px;\n}\n\n.member h3 {\n    margin: 10px 0 5px 0;\n}\n\n.member p {\n    margin: 0;\n    color: gray;\n}"],"sourceRoot":""}]);
